@import url(https://fonts.googleapis.com/css?family=Muli|Montserrat|Kodchasan|Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --theme-color: rgb(0, 148, 246);
}

body{
  margin: 0;
  background: #efefef;
}
header{
  background: rgb(0, 148, 246);
  background: var(--theme-color);
  padding: 0px 10px;
  display: flex;
}
.center{
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu{
  font-size: x-large;
  color: white;
  margin-right: 8px;
}
header > p{
  color: white;
  font-family: "Muli";
  font-size: x-large;
}
header > p > span{
  font-weight: bold;
}
main{
  padding: 12px;
  
  grid-gap: 5px;
  overflow: auto;
  height: 75vh;
}
.card{
  background-color: white;
  padding: 1px 20px;
  margin: 10px;
  border-radius: 3px;
  display: flex;
  flex-direction: column;
}

.card > h1{
  font-family: "Montserrat";
  font-size: large;
}
.card > p{  
  color: #333;
  font-size: small;
  font-family: "Roboto";
}

footer{
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.2);
  height: 50px;
  background-color: white;
}

.btn-add{
  margin-bottom: 25px;
  background-color: rgb(0, 148, 246);
  background-color: var(--theme-color);
  color: white;
  border-radius: 50%;
  padding: 20px;
  outline: none;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
.btn-add:active{
  color: rgb(0, 148, 246);
  color: var(--theme-color);
  background: white;
}
nav{
  position: fixed;
  top: 0px;
  height: 100%;
  background: white;
  width: 50%;
  z-index: 2;
  box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.3);
  -webkit-transition: 0.001s ease-in-out;
  transition: 0.001s ease-in-out;
}
.nav-header{
  background-color: rgb(0, 148, 246);
  background-color: var(--theme-color);
  color: white;

  padding: 1px 0px 1px 15px;
  display: flex;
}
.nav-header > p{
  margin-left: 10px;
  font-size: large;
  text-transform: capitalize;
  font-family: "Montserrat"
}
.menu-items > ul{
  list-style-type: none;
}
.menu-items > ul > li{
  padding: 2px 0px;
  font-family: "Muli";
  color: #333;
  margin: 2px 0px 2px 0px;
}
.menu-items > ul > li:hover{
  text-shadow: 2px 2px 0px rgba(0, 0, 0, 0.1);
  font-weight: bold;
}
.cover{
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.card-intro{
  position: fixed;
  background-color: white;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
  width: 50%;
  left: 25%; top: 20%;
  border-radius: 5px;
  padding: 10px;
  font-family: "Montserrat";
  flex-direction: column;
  z-index: 1;

}
a{
  text-decoration: inherit;
  color: inherit;
}
.card-cont{
  display: flex;
  align-self: center;
  background: white;
  margin: 10px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}
.icons{
  display: flex;
  align-self: center;
  margin-left: auto;
}
.icons > .fa{
  margin: 10px;
}
.btn-view{
  background: rgb(0, 148, 246);
  background: var(--theme-color);
  color: white;
  letter-spacing: 0.5px;
  border: none;
  outline: none;
  padding: 0px 20px;
}
#name{
  border: none;
  border-bottom: solid 1px rgb(0, 148, 246);
  border-bottom: solid 1px var(--theme-color);
  padding: 2px 2px;
  outline: none;
  font-family: "Kodchasan";
  text-align: center;
}
.btn-go{
  background-color: rgb(0, 148, 246);
  background-color: var(--theme-color);
  color: white;
  text-transform: uppercase;

  letter-spacing: 0.5px;
  border: none;
  box-shadow: 1px 1px 0px rgba(0, 0, 0, 0.3);
  margin: 10px;
  padding: 6px 12px;
  font-weight: bold;
  font-family: "Montserrat";
}
::-webkit-input-placeholder{
  font-family: "Kodchasan";
}
::-moz-placeholder{
  font-family: "Kodchasan";
}
:-ms-input-placeholder{
  font-family: "Kodchasan";
}
::-ms-input-placeholder{
  font-family: "Kodchasan";
}
::placeholder{
  font-family: "Kodchasan";
}
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(240, 235, 235); 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(0, 148, 246);
  background: var(--theme-color); 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

.note-reader{
  position: fixed;
  width: 100%;
  height: 100%;
  right: 100%;
  background: white;
}
.new_note_modal{
    position: fixed;
    width: 100%;
    height: 100%;
    background: white;
    padding: 0px;
    display: flex;
    flex-direction: column;
    -webkit-transition: 0.1s;
    transition: 0.1s;
}
.editor{
    margin: 10px;
    height: 60%;
    position: absolute;
    width: 85%;
    left: 5%;
}
.btn-group{
    position: absolute;
    top: 70%;
    left: 30%;
    width: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn-group > button{
    margin: 5px;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
    border: none;
    color: white;
    padding: 10px 16px;
    outline: none;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    font-family: 'Montserrat';
    background: #00C851;
}
.old_note_modal{
    position: fixed;
    width: 100%;
    height: 100%;
    background: white;
    -webkit-transition: 0.1s;
    transition: 0.1s;
    padding: -20px;
}

h1, h2, h3{
    margin-left: 10px;
}
p{
    margin-left: 10px;
}
ul{
    margin-left: 10px;
}
